import { useState } from 'react';
import PropTypes from 'prop-types';
import { FaSearch } from 'react-icons/fa';
// import { ToastContainer, toast } from 'react-toastify';

import styles from './SearchBar.module.scss';
// import 'react-toastify/dist/ReactToastify.min.css';

const SearchBar = ({ onSearch }) => {
  const [query, setQuery] = useState('');

  // –ù–∞–±–ª—é–¥–∞–µ—Ç –∑–∞ –∏–Ω–ø—É—Ç–æ–º –∏ –ø–∏—à–µ—Ç –∑–Ω–∞—á–Ω–∏–µ –≤ —Å—Ç–µ–π—Ç
  const handleSearchInput = e => {
    const { value } = e.target;

    setQuery(value);
  };

  // –ù–∞–±–ª—é–¥–∞–µ—Ç –∑–∞ –æ—Ç–ø—Ä–∞–≤–∫–æ–π –∏ –æ—Ç–¥–∞–µ—Ç –∑–Ω–∞—á–µ–Ω–∏–µ –≤–æ –≤–Ω–µ—à–Ω–∏–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç
  const handleSubmit = e => {
    e.preventDefault();

    if (!query.trim()) {
      alert('Please write your request');
      return;
    }

    onSearch(query);

    resetForm();
  };

  // –°–±—Ä–∞—Å—ã–≤–∞–µ—Ç –ø–æ–ª–µ –ø–æ—Å–ª–µ –æ—Ç–ø—Ä–∞–≤–∫–∏
  const resetForm = () => setQuery('');

  return (
    <>
      <form className={styles.FormSearch} onSubmit={handleSubmit}>
        <input
          className={styles.InputSearch}
          type="text"
          name="query"
          value={query}
          onChange={handleSearchInput}
          autoComplete="off"
          placeholder="Search movies"
          required
        />
        <button type="submit" className={styles.ButtonSubmit}>
          <FaSearch />
        </button>
      </form>

      {/* <ToastContainer /> */}
    </>
  );
};

SearchBar.propTypes = {
  onSearch: PropTypes.func.isRequired,
};

export default SearchBar;
